.container-sidebar{
    width: 20%;
}
.container-main-content{
    width: 80%;
}

.button-menu{
    display: none;
}

@media screen and (max-width:600px) {
    .container-sidebar{
        width: 100%;
        position: fixed;
        top:0;
        left: 0;
        background-color: aliceblue;
        z-index: 999
    }
    .display-none-resp{display: none;}
    .container-main-content{
        width: 100%;
    }
    .button-menu{
        display: block;
    }
    
}