.heroSlider .htSwiperButtonNav {
  font-size: 40px;
  width: 50px;
  height: 50px;
  padding: 0;
  color: #FFF;
  border: none;
  background: none;
}
.heroSlider .htSwiperButtonNav:after {
  font-size: 0;
}
.heroSlider .swiperButtonPrev {
  left: 50px;
}
.heroSlider .swiperButtonNext {
  right: 50px;
}

.sliderOne .slide {
  padding-top: 100px;
}

.slide .content h1 {
  line-height: 65px;
  font-size: 40px;
  margin-bottom: 25px;
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
  color: white;
}
.slide .content p {
  font-size: 16px;
  line-height: 26px;
  margin-bottom: 40px;
  color: #EEF3F6;
}
.slide .slideFrontImg {
  text-align: left;
}

@media only screen and (min-width: 1200px) and (max-width: 1599px) {
  .sliderOne .sliderFixedHeight {
    height: 700px;
  }
}
@media only screen and (min-width: 992px) and (max-width: 1199px) {
  .slide .slideFrontImg img {
    width: 100%;
  }
  .sliderOne .sliderFixedHeight {
    height: 700px;
  }
}
@media only screen and (min-width: 768px) and (max-width: 991px) {
  .heroSlider .htSwiperButtonNav {
    display: none;
  }
  .slide .content h1 {
    line-height: 49px;
  }
  .slide .slideFrontImg img {
    width: 100%;
  }
  .sliderOne .sliderFixedHeight {
    height: 700px;
  }
}
@media only screen and (max-width: 767px) {
  .heroSlider .htSwiperButtonNav {
    display: none;
  }
  .slide .content h1 {
    line-height: 41px;
    margin-bottom: 17px;
  }
  .slide .slideFrontImg img {
    width: 100%;
  }
  .sliderOne .sliderFixedHeight {
    height: auto;
  }
  .slide .slideFrontImg {
    margin-top: 26px;
    text-align: center;
  }
}
@keyframes Gradient {
  0% {
    background-position: 30% 50%;
  }
  50% {
    background-position: 25% 50%;
  }
  100% {
    background-position: 30% 50%;
  }
}
.heroSlider .htSwiperButtonNav {
  font-size: 40px;
  width: 50px;
  height: 50px;
  padding: 0;
  color: #FFF;
  border: none;
  background: none;
}
.heroSlider .htSwiperButtonNav:after {
  font-size: 0;
}
.heroSlider .swiperButtonPrev {
  left: 50px;
}
.heroSlider .swiperButtonNext {
  right: 50px;
}

.sliderTwo .slide {
  padding-bottom: 10px;
}

@media only screen and (min-width: 1200px) and (max-width: 1599px) {
  .sliderTwo .sliderFixedHeight {
    height: 700px;
  }
}
@media only screen and (min-width: 768px) and (max-width: 991px) {
  .sliderTwo .sliderFixedHeight {
    height: 700px;
  }
  .sliderTwo .slide {
    padding-top: 0px;
    padding-bottom: 0;
  }
}
@media only screen and (max-width: 767px) {
  .sliderTwo .sliderFixedHeight,
  .sliderOne .sliderFixedHeight {
    height: auto;
  }
  .sliderTwo .slide {
    padding-top: 0px;
    padding-bottom: 0;
  }
}